@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');


* {
  margin:0;
  padding:0;
  font-family: 'Ubuntu', sans-serif;
}

.wrapper {
  margin: 0 auto;
}


.plantDetail {
  border: 2px rgb(116, 168, 123) solid;
  padding: 30px;
  margin: 20px 130px;
  border-radius: 10px;
  /* width: 80%; */
  text-align: center;
}

.plantDetail h1 {
  margin-bottom: 30px;
  font-size: 40px;
  color:rgb(83, 136, 194);
  text-decoration: underline;
}

.plantDetailContent {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.plantDetailContent img {
  border: rgb(185, 185, 183) 3px solid;
  border-radius: 5px;
}

.plantDetailContentRight {
  margin-top: 10px;
  margin-left: 50px;
}

.plantDetailContentRight h4 {
  font-size: 20px;
  color: rgb(96, 95, 95);
  text-align: left;
}