  font-family: 'Ubuntu', sans-serif;
}

.wrapper {
  margin: 0 auto;
}


.wrapper header {
  margin: 30px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.wrapper header h1 {
  color: rgb(93, 146, 96);
  font-size: 50px;
  font-family: 'Ubuntu', sans-serif;
}

.leftHeader {
    display: flex;
    align-items: baseline;
}


