* {
  margin:0;
  padding:0;
  font-family: 'Ubuntu', sans-serif;
}

.wrapper {
  margin: 0 auto;
}


.landingDisplay {
  border: 2px rgb(116, 168, 123) solid;
  padding: 30px;
  margin: 20px 50px;
  border-radius: 5px;
  text-align: center;
}

.landingDisplay h2 {
    font-family: 'Ubuntu', sans-serif;
    color:rgb(86, 91, 86);
    font-size: 30px;
}


.list {
  margin: 50px 0 0 0;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  font-family: 'Ubuntu', sans-serif;
}

.searchBox {
    margin: 0 auto;
    margin-left: 37%;
}

.plantList {
    margin: 0 10px 20px 10px;
    padding-top: 15px;
    color:rgb(73, 71, 53);
    border: rgb(162, 219, 162) 3px solid;
    border-radius: 5px;
    width: 330px;
    height: 300px;
}

.plantApiImage {
  border: rgb(235, 226, 145) 3px solid;
  border: rgb(235, 232, 145) 3px solid;
  border-radius: 5px;
}

.plantListDetail {
  text-align: left;
  margin: 20px 0 0 10px;
}

.plantListDetail h3 {
    margin-bottom: -15px;
    font-size: 15px;
}

.plantListDetail h3 span {
    font-weight: bold;
    font-size: 17px;
}
